@font-face {
    font-family: 'BluuNext Titling';
    font-style: normal;
    font-weight: normal;
    src: local('BluuNext Titling'), url('../fonts/BluuNext Titling.woff') format('woff');
}
.home-img{
    position: relative;
    text-align: center;
    
  }
.home-img img{
    width: 100%;
    height:  auto;
} 
/**********************************************/
.home-anim{
    position: absolute;
    top: 40%;
    left: 40%;
    transform: translate(-100%, -50%);
    border: 3px solid rgb(9, 23, 146);
    padding: 5px;
  } 
  .home-anim-img{
      border: 1px solid yellow;
  }
  .home-anim-img img{
      width: 100%;
      opacity: 0.8;

  } 
   .anim-text{
    border: 1px solid white;
    position: absolute;
    bottom: 8px;
  left: 16px;
   
    
  } 
    .anim-text p{
      width: 100%;
      color:transparent;
      -webkit-text-stroke: 1px rgb(255, 255, 255);
      font-family: 'BluuNext Titling';
      font-size: 80px;
      font-weight:700;
      
      /* text-align:left; */
      line-height:65px;
      vertical-align:baseline;
      letter-spacing:normal;
      word-spacing:0px;
     
      
    
  }  

 